<template><section><html><head></head><body><h1>场景容器</h1>
<p><code v-pre="">vc-viewer</code> 是用于构建 Cesium 应用程序的基础组件，场景容器的实质是通过 Cesium.Viewer 初始化的一个 DOM 节点，用于挂载其他 DOM 节点或者组件。默认影像底图改为用<code v-pre="">NaturalEarthII</code>影像了，如果需要二次开发或者手动控制其子组件，可以在 <code v-pre="">ready</code> 事件中使用返回的 <code v-pre="">Cesium</code> 和 <code v-pre="">Viewer</code> 实例进行手动控制。在<code v-pre="">2.0.1+</code>版本中可以通过<code v-pre="">ref</code>来获取组件的<code v-pre="">createPromise</code>对象来执行相关操作。</p>
<h2>示例</h2>
<h3>用 vc-viewer 组件初始化 Viewer 实例</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer" ref="viewerContainer">
      <vc-viewer ref="vcViewer" :animation="animation" :base-layer-picker="baseLayerPicker" :timeline="timeline" :fullscreen-button="fullscreenButton" :fullscreen-element="fullscreenElement" :info-box="infoBox" @ready="ready" @left-click="leftClick">
        <vc-navigation :options="options"></vc-navigation>
        <vc-map-overview></vc-map-overview>
        <vc-layer-imagery>
          <vc-provider-imagery-tianditu map-style="img_c" :token="tk"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
        <vc-layer-imagery ref="layerText">
          <vc-provider-imagery-tianditu map-style="cia_c" :token="tk"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <span>动画部件</span>
        <md-switch v-model="animation"></md-switch>
        <span>时间轴部件</span>
        <md-switch v-model="timeline"></md-switch>
        <span>基础图层拾取器</span>
        <md-switch v-model="baseLayerPicker"></md-switch>
        <span>全屏按钮</span>
        <md-switch v-model="fullscreenButton"></md-switch>
        <span>信息提示框</span>
        <md-switch v-model="infoBox"></md-switch>
      </div>
    </div>
  </template>
  
</doc-preview>
