    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span>
      <span class="hljs-attr">ref</span>=<span class="hljs-string">"vcViewer"</span>
      <span class="hljs-attr">:animation</span>=<span class="hljs-string">"animation"</span>
