<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>animation</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code>是否显示动画控件。</td>
</tr>
<tr>
<td>baseLayerPicker</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code>是否显示基础图层切换按钮。</td>
</tr>
<tr>
<td>fullscreenButton</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code>是否显示全屏切换按钮。</td>
</tr>
<tr>
<td>vrButton</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code>是否显示 VR 功能按钮。</td>
</tr>
<tr>
<td>geocoder</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code>是否显示地理编码器搜索框。</td>
</tr>
<tr>
<td>homeButton</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code>是否显示主页按钮。</td>
</tr>
<tr>
<td>infoBox</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code>是否显示信息框。</td>
</tr>
<tr>
<td>sceneMode</td>
<td>Number</td>
<td><code v-pre="">3</code></td>
<td><code v-pre="">optional</code> 指定场景模式。<code v-pre="">1</code> 哥伦布视图，<code v-pre="">2</code> 二维视图， <code v-pre="">3</code> 三维视图。</td>
</tr>
<tr>
<td>sceneModePicker</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code>是否显示场景模式切换按钮。</td>
</tr>
<tr>
<td>selectionIndicator</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code>是否显示选择指示符。</td>
</tr>
<tr>
<td>timeline</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code>是否显示时间轴控件。</td>
</tr>
<tr>
<td>navigationHelpButton</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code>是否显示导航帮助按钮。</td>
</tr>
<tr>
<td>navigationInstructionsInitiallyVisible</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code>是展开导航帮助面板，否点击navigationHelpButton才能展开面板。</td>
</tr>
<tr>
<td>scene3DOnly</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code>如果为true，则每个几何实例仅以3D形式呈现以节省GPU内存。</td>
</tr>
<tr>
<td>shouldAnimate</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code>true if the clock should attempt to advance simulation time by default, false otherwise.</td>
</tr>
<tr>
<td>fullscreenElement</td>
<td>Element | String</td>
<td><code v-pre="">document.body</code></td>
<td><code v-pre="">optional</code>按下全屏按钮时要放入全屏模式的元素或ID。</td>
</tr>
<tr>
<td>useDefaultRenderLoop</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code>如果此部件能够控制渲染循环，设置为true，反之设置为false。</td>
</tr>
<tr>
<td>targetFrameRate</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code>使用默认渲染循环时的目标帧速率。</td>
</tr>
<tr>
<td>showRenderLoopErrors</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code>如果设置为true，发生渲染循环错误时，将自动给用户显示一个包含错误信息的HTML面板。</td>
</tr>
<tr>
<td>automaticallyTrackDataSourceClocks</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code>如果设置为true，将自动跟踪新添加数据源的时钟设置，如果数据源的时钟变更，则更新。如需单独设置时钟，请将此项设置为false。</td>
</tr>
<tr>
<td>contextOptions</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code>Context and WebGL 创建属性与传递给Scene匹配的选项。</td>
</tr>
<tr>
<td>orderIndependentTranslucency</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code>如果此项设置为true，并且使用设备支持，将使用与顺序无关的半透明。</td>
</tr>
<tr>
<td>creditContainer</td>
<td>Element | String</td>
<td></td>
<td><code v-pre="">optional</code>指定包含CreditDisplay信息的DOM元素或ID。如若未指定，credit信息将添加到部件底部。</td>
</tr>
<tr>
<td>creditViewport</td>
<td>Element | String</td>
<td></td>
<td><code v-pre="">optional</code>指定包含CreditDisplay弹出框信息的DOM元素或ID。如若未指定，credit信息将添加到部件底部。</td>
</tr>
<tr>
<td>terrainExaggeration</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code>用于夸大地形的标量。请注意，设置地形夸张不会修改其它任何数据。</td>
</tr>
<tr>
<td>shadows</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code>确定阴影是否由太阳投射形成。</td>
</tr>
<tr>
<td>terrainShadows</td>
<td>Number</td>
<td><code v-pre="">3</code></td>
<td><code v-pre="">optional</code>确定地形是否投射或接受来自太阳的阴影。</td>
</tr>
<tr>
<td>mapMode2D</td>
<td>Number</td>
<td><code v-pre="">1</code></td>
<td><code v-pre="">optional</code>确定二维地图是可旋转的或是可以在在水平方向上无限滚动。</td>
</tr>
<tr>
<td>projectionPicker</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code>是否显示投影切换按钮</td>
</tr>
<tr>
<td>logo</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 是否显示默认logo.</td>
</tr>
<tr>
<td>accessToken</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code>指定accessToken，使用Cesium ion的数据源需要到<a href="https://cesium.com/ion/">https://cesium.com/ion/</a>申请一个账户，获取Access Token。</td>
</tr>
<tr>
<td>cesiumPath</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code>指定当前场景使用的 cesium 版本。一般是Vue.use()的时候指定。</td>
</tr>
<tr>
<td>camera</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 场景相机位置.默认定位到全球范围内的中国。 <strong>结构：{ position: { lng: number, lat: number, height: number }, heading: number, pitch: number, roll: number }</strong></td>
</tr>
<tr>
<td>TZcode</td>
<td>String</td>
<td><code v-pre="">UTM</code></td>
<td><code v-pre="">optional</code> 时区代码。</td>
</tr>
<tr>
<td>UTCoffset</td>
<td>String</td>
<td><code v-pre="">-(new Date().getTimezoneOffset())</code></td>
<td><code v-pre="">optional</code> UTC时间的时差（分钟）。</td>
</tr>
<tr>
<td>removeCesiumScript</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> vc-viewer销毁时是否移除CesiumJS标签.</td>
</tr>
<tr>
<td>autoSortImageryLayers</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 添加影像图层时是否根据图层sortOrder属性自动排序。</td>
</tr>
<tr>
<td>viewerCreator</td>
<td>Function</td>
<td></td>
<td><code v-pre="">optional</code> 自定义 viewer 初始化的方法，需要返回 viewer 实例。</td>
</tr>
</tbody>
</table>
<hr>
<h2>事件</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
<th>来源</th>
</tr>
</thead>
<tbody>
<tr>
<td>selectedEntityChanged</td>
<td>entity</td>
<td>场景选中实体发生改变时触发此事件。事件参数表示选中的实体，或者undefined（为选中）</td>
<td>Viewer</td>
</tr>
<tr>
<td>trackedEntityChanged</td>
<td>entity</td>
